import React, { useReducer, useState, useEffect, useContext } from 'react';
import {postReducer} from '../Api/Reducers';
import {fetchPosts} from '../Api/ActionCreators';
import PostCard from './PostCard';
import Loading from '../Utility/Loading';
import '../../css/Posts.css';
import grid from '../../images/grid.png';
import list from '../../images/list.png';
import gridActive from '../../images/grid-active.png';
import listActive from '../../images/list-active.png';
// import {formatDate} from '../Hooks';
import { UserContext } from '../Context';

export default function PostList(){
    document.title =  "Posts | CodeUnlock.in"
    const [posts, dispatch] = useReducer(postReducer, {loading: true})
    // const user = useContext(UserContext);

    useEffect(()=>{
        fetchPosts(dispatch);
    }, [])

    return (
        <section className="posts">
            {posts.loading && (<Loading />)}
            {!posts.loading && (
            <>
                <div className="post-info">
                    <div className="post-info-child">
                        <div className="post-header">
                            {/* <i className="fas fa-list-alt" style={{fontSize: "20px", marginRight: "4px"}}></i> */}
                            <h1 className="post_count">All Posts</h1>
                        </div>
                        <PostListView />
                    </div>
                </div> 
                <ul className="post-list">
                {posts.posts.length > 0 && posts.posts.map((post, index) => (
                        <PostCard key={index} index={index} post={post} />
                    )
                )}
                {posts.posts.length === 0 && <div style={{fontStyle: "italic"}}>No posts available at the moment!</div>}
                </ul>
            </>
            )} 
        </section>   
    )
}

export const BookmarkedPost = (props) => {
    if(props?.setTitle !== false)
        document.title =  "Bookmarked Posts | CodeUnlock.in"
    const [posts, dispatch] = useReducer(postReducer, {loading: true})
    const user = useContext(UserContext).userState;

    useEffect(()=>{
        fetchPosts(dispatch, user.id, "bookmarks");
    }, [user.id])

    return (
        <section className="posts">
            {posts.loading && (<Loading />)}
            {!posts.loading && (
                <>
                <div className="post-info">
                    <div className="post-info-child">
                        <div className="post-header">
                            <i className="fas fa-bookmark" style={{fontSize: "20px", marginRight: "4px"}}></i>
                            <h1 className="post_count">Bookmarks</h1>
                        </div>
                        <PostListView />
                    </div>
                </div>
                <ul className="post-list">
                {posts.posts.length > 0 && posts.posts.map((post, index) => <PostCard key={index} index={index} post={post} />)}
                {posts.posts.length === 0 && <div style={{fontStyle: "italic"}}>You haven't bookmarked any post at the moment!</div>}
                </ul>
                </>
            )} 

        </section>            
    )
}

